
@import "../../scss/variables";

.wp-block-jl-testimonial {
  border: .1em solid #aaaaaa;
  border-radius: .5em;
  color: black;
  margin: 0 auto;
  background-color: $bg-gray;

  &.bg-primary-blue {
	background-color: $primary-blue;
  }

  &.bg-primary-red {
	background-color: $primary-red;
  }

  .stars {
	text-align: center;
	font-size: 2em;
	letter-spacing: .5em;
	margin-top: .5em;
	color: $star-color;

	select {
	  width: 10em;
	}
  }

  .quote {
	width: 100%;
	padding: 1.5em;
	line-height: 1.5;
  }

  .quote-profile {
	background-color: $bg-light;
	color: $text-color;
	border-radius: 0 0 .5em .5em;
	padding: 1em;
	display: flex;
	justify-content: space-between;
	align-items: center;

	.photo img {
	  width: 75px;
	  height: 75px;
	  object-fit: cover;
	  border-radius: 50%;
	  margin-right: 2em;
	}

	.author, .location{
	  margin: 0;
	  text-align: right;
	  font-weight: 700;
	}

	.location {
	  font-weight: 300;
	  font-size: 0.7em;
	}
  }
}
